@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format(woff2), url("../fonts/opensans.woff") format(woff);
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format(woff2), url("../fonts/opensansbold.woff") format(woff);
}
.container {
  margin: 0 auto;
  padding: 0 30px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .container {
    padding: 0 50px;
  }
}
@media (min-width: 1150px) {
  .container {
    max-width: 1150px;
  }
}

.button {
  box-sizing: border-box;
  display: inline-flex;
  font-family: inherit;
  margin: 0;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: #444444;
  text-transform: uppercase;
  font-weight: 700;
  cursor: pointer;
  background-color: transparent;
}
.button--primary {
  font-size: 18px;
  line-height: 20px;
  padding: 13px 20px;
  border: 2px solid #444444;
}
@media (min-width: 768px) {
  .button--primary {
    font-size: 17px;
  }
}
.button--secondary {
  font-size: 17px;
  line-height: 16px;
  color: #ffffff;
  background-color: #63d1bb;
  padding: 18px 20px;
}
.button:hover, .button:focus {
  color: #ffffff;
  background-color: #444444;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}

.input {
  font-family: inherit;
  font-size: 17px;
  line-height: 18px;
  text-align: left;
  color: #444444;
  border-bottom: 2px solid #000000;
  background: transparent;
  box-sizing: border-box;
  width: 100%;
  box-shadow: inset 0 -2px 0 #000000;
}
@media (min-width: 768px) {
  .input {
    font-size: 19px;
    padding-top: 16px;
    padding-bottom: 18px;
    padding-left: 18px;
  }
}
.input:hover, .input:focus-within {
  box-shadow: inset 0 -2px 0 #62d1ba;
}
.input:active, .input:focus {
  box-shadow: inset 0 -2px 0 #62d1ba;
}

.form-radio__label {
  display: flex;
  font-family: inherit;
  font-size: 17px;
  font-weight: 700;
  line-height: 18px;
  text-align: left;
  color: #000000;
  align-items: center;
}
@media (min-width: 768px) {
  .form-radio__label {
    font-size: 19px;
    line-height: 18px;
  }
}

.form-radio__mark {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 42px;
  min-width: 42px;
  border: 2px solid #444444;
  border-radius: 100%;
  margin-right: 19px;
  box-sizing: border-box;
  flex-shrink: 0;
}
@media (min-width: 768px) {
  .form-radio__mark {
    min-height: 52px;
    min-width: 52px;
    margin-right: 26px;
  }
}

.form-radio:not(:first-of-type) {
  margin-top: 11px;
}
@media (min-width: 768px) {
  .form-radio:not(:first-of-type) {
    margin-top: 24px;
  }
}

.form-radio__input:checked + .form-radio__mark::before {
  background-color: #62d1ba;
  content: "";
  width: 18px;
  height: 18px;
  border-radius: 100%;
}

.form-radio:hover .form-radio__mark, .form-radio:focus-visible .form-radio__mark {
  border-color: #62d1ba;
}
.form-radio:active .form-radio__mark {
  border-color: rgba(68, 68, 68, 0.3);
}

.form-checkbox__label {
  display: flex;
  font-family: inherit;
  font-size: 17px;
  font-weight: 700;
  line-height: 18px;
  text-align: left;
  color: #000000;
  align-items: center;
}
@media (min-width: 768px) {
  .form-checkbox__label {
    font-size: 19px;
    line-height: 18px;
  }
}

.form-checkbox__mark {
  display: flex;
  justify-content: center;
  align-items: center;
  content: "";
  border: 2px solid #444444;
  min-height: 38px;
  min-width: 38px;
  margin-right: 23px;
  box-sizing: border-box;
  flex-shrink: 0;
}
@media (min-width: 768px) {
  .form-checkbox__mark {
    height: 52px;
    width: 52px;
    margin-right: 29px;
  }
}

@media (max-width: 767px) {
  .form-checkbox:not(:first-of-type) {
    margin-top: 23px;
  }
}

.form-checkbox__input:checked + .form-checkbox__mark::before {
  content: "";
  width: 23px;
  height: 16px;
  background-image: url("../img/decoration/checkbox-m.svg");
  background-repeat: no-repeat;
  background-position: center;
}
@media (min-width: 768px) {
  .form-checkbox__input:checked + .form-checkbox__mark::before {
    background-image: url("../img/decoration/checkbox-t.svg");
    width: 32px;
    height: 22px;
  }
}

.form-checkbox:hover .form-checkbox__mark, .form-checkbox:focus-visible .form-checkbox__mark {
  border-color: #62d1ba;
}
.form-checkbox:active .form-checkbox__mark {
  border-color: rgba(68, 68, 68, 0.3);
}

.visually-hidden {
  position: absolute;
  max-width: 1px;
  max-height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page__body {
  margin: 0 auto;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 30px;
  text-align: center;
  color: #444444;
  background-color: #ffffff;
  min-width: 320px;
}

.page-main__item {
  padding: 74px 30px;
  background-color: #f1f1f1;
}
@media (min-width: 768px) {
  .page-main__item {
    position: relative;
    padding: 101px 80px 87px 80px;
  }
}
@media (min-width: 1150px) {
  .page-main__item {
    padding: 101px 100px 113px 100px;
  }
}

@media (min-width: 768px) {
  .page-main__item::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 24px;
    left: 0;
    bottom: -1px;
    background-image: url("../img/decoration/wave-t.svg");
    background-repeat: repeat-x;
    background-position: bottom;
  }
}
@media (min-width: 1150px) {
  .page-main__item::after {
    height: 34px;
    bottom: 0;
    background-image: url("../img/decoration/wave-d.svg");
  }
}

.page-main__item-title {
  font-size: 27px;
  font-weight: 700;
  color: #000000;
  margin: 0;
}
@media (min-width: 768px) {
  .page-main__item-title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .page-main__item-title {
    font-size: 53px;
    line-height: 57px;
  }
}

.page-form__text {
  font-size: 13px;
  line-height: 24px;
  text-align: left;
  margin: 17px auto 0 auto;
  padding: 0 30px;
}
@media (min-width: 768px) {
  .page-form__text {
    font-size: 17px;
    line-height: 30px;
    margin-top: 54px;
    padding: 0 50px;
  }
}
@media (min-width: 1150px) {
  .page-form__text {
    margin-top: 99px;
    max-width: 702px;
  }
}

.page-header {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 79px;
}
@media (min-width: 768px) {
  .page-header {
    justify-content: start;
    margin-bottom: -44px;
    z-index: 1;
  }
}
@media (min-width: 1150px) {
  .page-header {
    justify-content: center;
  }
}
@media (max-width: 767px) {
  .page-header--no-js {
    display: flex;
    flex-direction: column;
  }
}
.page-header--no-js .page-header__toggle {
  display: none;
}

@media (min-width: 768px) {
  .page-header::after {
    position: absolute;
    content: "";
    width: 100%;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    left: 0;
    top: 88px;
  }
}

@media (min-width: 768px) {
  .page-header__wrapper {
    position: relative;
    width: 100%;
  }
}

.page-header__logo {
  display: inline-block;
  z-index: 2;
  padding: 18px 0 14px 0;
}
@media (min-width: 768px) and (max-width: 1149px) {
  .page-header__logo {
    position: absolute;
    top: 0;
    left: 50px;
    padding: 22px 0 20px 0;
  }
}
@media (min-width: 1150px) {
  .page-header__logo {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    padding: 9px 0 0 0;
  }
}
.page-header__logo:hover, .page-header__logo:focus {
  opacity: 0.6;
}
.page-header__logo:active {
  opacity: 0.3;
}

.page-header__logo-img {
  vertical-align: bottom;
}
@media (min-width: 768px) {
  .page-header__logo-img {
    width: 113px;
    height: 46px;
  }
}
@media (min-width: 1150px) {
  .page-header__logo-img {
    width: 149px;
    height: 72px;
  }
}

.page-header__toggle {
  position: absolute;
  border: none;
  width: 21px;
  height: 20px;
  right: 31px;
  top: 29px;
  background-color: inherit;
  background-image: url("../img/decoration/menu-open.svg");
  background-repeat: no-repeat;
  background-position: center center;
  transition: 0.3s;
}
@media (min-width: 768px) {
  .page-header__toggle {
    display: none;
  }
}

.page-header__toggle--clossed {
  background-image: url("../img/decoration/menu-close.svg");
}

.site-list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.site-link {
  text-decoration: none;
  color: inherit;
}

.navigation {
  font-size: 17px;
  font-weight: 700;
  line-height: 24px;
  color: #000000;
  text-align: left;
}
@media (max-width: 767px) {
  .navigation {
    display: none;
  }
}
@media (min-width: 768px) {
  .navigation {
    display: grid;
    grid-template-columns: 1fr 348px;
    grid-template-areas: ". user" "basic aftertext";
    box-sizing: border-box;
    max-width: 1150px;
    margin: 0 auto;
    padding: 0 50px;
  }
}
@media (min-width: 1150px) {
  .navigation {
    grid-template-columns: 1fr 160px 1fr;
    grid-template-areas: "basic . user" "extra . aftertext";
  }
}
@media (max-width: 767px) {
  .navigation--no-js {
    display: block;
    width: 100%;
  }
}

@media (max-width: 767px) {
  .navigation--open {
    display: block;
    z-index: 1;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background-color: #ffffff;
  }
}

@media (max-width: 767px) {
  .navigation__list {
    width: 100%;
  }
}
@media (min-width: 768px) {
  .navigation__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
}

.navigation__list--basic {
  grid-area: basic;
}
@media (min-width: 768px) and (max-width: 1149px) {
  .navigation__list--basic {
    margin-top: 20px;
  }
}

@media (min-width: 1150px) {
  .navigation__list--basic .navigation__item:nth-child(2n) {
    border-left: 1px solid rgba(0, 0, 0, 0.1);
  }
}

.navigation__list--user {
  align-self: start;
  grid-area: user;
}
@media (min-width: 1150px) {
  .navigation__list--user {
    margin-left: auto;
  }
}

.navigation__list--extra {
  display: none;
  grid-area: extra;
}
@media (min-width: 1150px) {
  .navigation__list--extra {
    display: flex;
    font-size: 15px;
  }
}

.navigation__list--extra .navigation__item {
  padding-top: 20px;
}

@media (max-width: 767px) {
  .navigation__item {
    position: relative;
    background-color: #ffffff;
    border-top: 1px solid #ececec;
  }
}

.navigation__item--extra:not(:first-of-type) {
  margin-left: 64px;
}

@media (min-width: 768px) {
  .navigation__item-text {
    display: none;
  }
}

@media (min-width: 1150px) {
  .navigation__item--basic:first-of-type .navigation__link {
    padding-left: 0;
    margin-right: 36px;
  }
}

@media (min-width: 768px) and (max-width: 1149px) {
  .navigation__item--basic:nth-child(2n) .navigation__link {
    margin-left: 36px;
  }
}
@media (min-width: 1150px) {
  .navigation__item--basic:nth-child(2n) .navigation__link {
    padding-right: 30px;
  }
}

@media (min-width: 1150px) {
  .navigation__item--basic:nth-child(3n) .navigation__link {
    padding-left: 0;
    margin-right: 36px;
  }
}

.navigation__link {
  transition: 0.3s;
}
@media (max-width: 767px) {
  .navigation__link {
    display: block;
    position: relative;
    padding: 28px 30px 26px 76px;
  }
}
@media (min-width: 768px) {
  .navigation__link {
    display: flex;
    align-items: center;
  }
}

.navigation__link:hover .navigation__icon,
.navigation__link:focus .navigation__icon {
  fill: #63d1bb;
  opacity: 0.6;
  transition: 0.3s;
}

.navigation__link:active .navigation__icon {
  fill: #63d1bb;
  opacity: 0.3;
  transition: 0.3s;
}

@media (min-width: 768px) and (max-width: 1149px) {
  .navigation__link--basic {
    font-size: 15px;
  }
}
@media (min-width: 1150px) {
  .navigation__link--basic {
    font-size: 17px;
    padding: 33px 0 32px 35px;
  }
}
.navigation__link--basic:hover, .navigation__link--basic:focus {
  opacity: 0.6;
}
.navigation__link--basic:active {
  opacity: 0.3;
}

.navigation__link--extra:hover,
.navigation__link--extra:focus {
  opacity: 0.6;
}

.navigation__link--extra:active {
  opacity: 0.3;
}

@media (max-width: 767px) {
  .navigation__link--search,
  .navigation__link--basket {
    position: relative;
    padding-left: 30px;
  }
}
@media (min-width: 768px) {
  .navigation__link--search,
  .navigation__link--basket {
    display: flex;
    align-items: center;
    text-align: left;
    box-sizing: border-box;
  }
}

@media (min-width: 768px) {
  .navigation__link--search {
    border-left: 1px solid rgba(0, 0, 0, 0.1);
    border-right: 1px solid rgba(0, 0, 0, 0.1);
    padding: 36px 31px 34px 31px;
  }
}

@media (min-width: 768px) {
  .navigation__link--basket {
    padding: 32px 0 32px 30px;
  }
}
@media (min-width: 1150px) {
  .navigation__link--basket {
    padding-left: 33px;
  }
}

.navigation__text {
  grid-area: aftertext;
  font-size: 15px;
  font-weight: 400;
  line-height: 24px;
  color: #444444;
  margin-top: 20px;
  text-align: right;
}
@media (max-width: 767px) {
  .navigation__text {
    display: none;
  }
}

.navigation__icon {
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0.2;
}

@media (max-width: 767px) {
  .navigation__icon--basket {
    margin-right: 25px;
  }
}
@media (min-width: 768px) {
  .navigation__icon--basket {
    margin-right: 32px;
  }
}

@media (max-width: 767px) {
  .navigation__icon--search {
    margin-right: 26px;
  }
}

.info-container {
  padding-top: 73px;
  padding-bottom: 120px;
}
@media (min-width: 768px) {
  .info-container {
    padding-top: 155px;
    padding-bottom: 372px;
  }
}
@media (min-width: 1150px) {
  .info-container {
    padding-top: 158px;
    padding-bottom: 355px;
  }
}

.info {
  position: relative;
  background-color: #fbfbfb;
  background-image: url("../img/info-m.jpg");
  background-repeat: no-repeat;
  background-position: center top -18px;
}
@media (min-resolution: 2dppx) {
  .info {
    background-image: url("../img/info-m@2x.jpg");
    background-size: 320px auto;
  }
}
@media (min-width: 768px) {
  .info {
    background-image: url("../img/info-t.jpg");
    background-position: center bottom 12px;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .info {
    background-image: url("../img/info-t@2x.jpg");
    background-size: 768px auto;
  }
}
@media (min-width: 1150px) {
  .info {
    background-image: url("../img/info-d.jpg");
    background-position: center bottom 13px;
  }
}
@media (min-width: 1150px) and (min-resolution: 2dppx) {
  .info {
    background-image: url("../img/info-d@2x.jpg");
    background-size: 1150px auto;
  }
}

@media (min-width: 768px) {
  .info::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 24px;
    left: 0;
    bottom: -1px;
    background-image: url("../img/decoration/wave-t.svg");
    background-repeat: repeat-x;
    background-position: bottom;
  }
}
@media (min-width: 1150px) {
  .info::after {
    height: 34px;
    bottom: -5px;
    background-image: url("../img/decoration/wave-d.svg");
  }
}

.info__title {
  font-size: 27px;
  font-weight: 700;
  color: #000000;
  max-width: 260px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .info__title {
    font-size: 43px;
    line-height: 50px;
    max-width: 668px;
  }
}
@media (min-width: 1150px) {
  .info__title {
    font-size: 53px;
    line-height: 57px;
    max-width: 700px;
  }
}

.info__wrapper {
  padding: 0;
  margin: 0 auto;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .info__wrapper {
    display: flex;
    flex-wrap: wrap;
    padding: 0 50px;
  }
