.info__button--cube {
  background-color: #56c3b2;
  background-image: url("../img/button-cube.svg");
  background-repeat: no-repeat;
  background-position: top 21px right 27px;
}
@media (min-width: 768px) {
  .info__button--cube {
    background-position: top 31px right 33px;
  }
}
@media (min-width: 1150px) {
  .info__button--cube {
    background-position: top 31px right 35px;
  }
}
.info__button--cube:hover, .info__button--cube:focus-visible {
  background-color: #46c1ae;
}

.featured {
  padding-top: 31px;
  padding-bottom: 42px;
  margin: 0 auto;
  background-image: url("../img/decoration/zigzag-small.svg");
  background-repeat: repeat-x;
  background-position: bottom;
  text-align: left;
}
@media (min-width: 768px) {
  .featured {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-areas: "ftitle ftitle ftitle ftopweek" "ftext ftext fimg fimg" "flist flist fimg fimg" "fprice fprice fimg fimg";
    padding-top: 39px;
    padding-bottom: 69px;
    background-image: url("../img/decoration/zigzag-medium.svg");
  }
}
@media (min-width: 1150px) {
  .featured {
    grid-template-columns: 100px 307px 525px;
    grid-template-areas: "ftopweek ftitle fimg" "ftopweek ftext fimg" "ftopweek flist fimg" "ftopweek fprice fimg";
    grid-template-rows: 24% 22% 1fr 1fr;
    column-gap: 43px;
    padding-top: 59px;
    padding-bottom: 94px;
    padding-left: 81px;
    background-image: url("../img/decoration/zigzag-large.svg");
    background-size: 1050px auto;
    background-repeat: no-repeat;
  }
}

.featured__title {
  grid-area: ftitle;
  font-size: 25px;
  font-weight: 700;
  line-height: 30px;
  color: #000000;
  margin: 0;
}
@media (min-width: 768px) {
  .featured__title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .featured__title {
    font-size: 53px;
    line-height: 57px;
    z-index: 1;
    padding-top: 61px;
    align-self: start;
  }
}

.featured__topweek-titel {
  grid-area: ftopweek;
  font-size: 17px;
  font-weight: 700;
  line-height: 20px;
